.QuestionnaireCardContact {
    display: flex;

    &__Container {
        backdrop-filter: blur(30px);
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        height: 423px;
        width: 80%;
        margin: auto;
        border-radius: 10px;

        & > .CTA {
            margin: auto;
        }

        & > .InputBox {
            margin: 20px auto 0;
            height: 40px;
            width: 180px;
        }
    }
    
}