.Ticket {
    position: absolute;
    right: 0;
    top: 1px;
    cursor: pointer;

    &__Text {
        position: absolute;
        top: 7px;
        left: 18px;
        font-size: 9px;
        font-family: Arial, Helvetica, sans-serif;
        color: white;
        font-weight: bold;
        text-align: center;
    }
}

.Closed {
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-weight: bold;
    top: 50px;
    text-align: center;

    & .Ticket__Text {
        top: 5px;
        font-size: 6px;
    }
}