@import '../../assets/mixins/mixins.scss';

.Hero {
    background-color: #242B3A;
    display: flex;
    padding-bottom: 312px;

    &__Left-Container {
        width: 100%;
        padding-top: 98px;
        display: flex;
        flex-direction: column;
    }

    &__Right-Container {
    }
}

@include tablet-p {
    
    .Hero {
        height: 575px;
        background-color: #242B3A;
        display: flex;
    
        &__Left-Container {
            width: 50%;
            padding-left: 0px;
            padding-top: 121px;
        }
    
        &__Right-Container {
            display: inline-block;
            width: 50%;
            position: relative;
        }
    }
}