@import '../../../assets/mixins/mixins.scss';

.QuestionnaireCardBill {
    display: flex;
    flex-direction: column;
    height: 100%;

    &__Blur {
        margin: auto;
        width: 90%;
        backdrop-filter: blur(30px);
        padding: 0 0 20px 0;
        border-radius: 10px;
         
        &__Container {
            margin: auto;
            z-index: 10;
            height: 350px;
            display: inline-flex;
            flex-direction: column;
            justify-content: space-around;
            

            &__Top {
                display: flex;
                }

            &__Counter {
                margin-top: -40px;
                margin-bottom: 020px;
                color: white;
                font-size: 36px;
                text-align: center;
                }

            &__Bottom {
                display: flex;
                flex-direction: column;
                margin: 100px 0 0;
                justify-content: center;
                align-content: center;
                justify-items: center;
                padding-bottom: 20px;

                &__Input[type=range] {
                    margin: 0 auto 50px;
                    width: 80%;
                    -webkit-appearance: none;
                    
                    &::-webkit-slider-runnable-track {
                        background-color: #707070;
                        height: 10px;
                        width: 10px;
                        border-radius: 2px;
                    }

                    &::-webkit-slider-thumb {
                        box-shadow: 0px 0px 0px #000000;
                        border: 0px solid #000000;
                        height: 20px;
                        width: 25px;
                        border-radius: 7px;
                        background: #464646;
                        cursor: pointer;
                        -webkit-appearance: none;
                        margin-top: -3.5px;
                    }

                    &:focus {
                        outline: none;
                    }
                }

                & > .CTA {
                    margin: auto;
                }
            }
        }
    }
}

@include desktop {

    .QuestionnaireCardBill {
        width: 80%;
        margin: auto;

        &__Blur {

        }
    }
}