@import '../../assets/mixins/mixins.scss';//1.632

.Questionnaire {
    box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
    background-image: url('../../assets/imgs/solar.jpeg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    position: relative;
    width: 303px;
    height: 161px;
    margin: 22px auto 0;

    //width: 1.67
    //height: 2.5
    &__Container {
        margin: auto;
        color: #FFF;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        text-align: center;
        backdrop-filter: blur(30px);
        width: 261px;
        height: 114.4px;
        border-radius: 10px;
        

        &__Holder {
            padding: 20px 40px;
            width: 261px;
            display: flex;
            flex-direction: column;
            &__Title {
                margin-bottom: 5px;
                font-size: 20px;
                font-family: 'Montserrat', sans-serif;
            }

            &__Options {
                margin: 0 auto;
                display: flex;

                &__Container {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 100%;

                    &__Zip {
                        height: fit-content;
                    }

                    &__Options {
                        display: flex;
                        width: fit-content;
                        justify-content: space-evenly;
                        &__Yes {
                            cursor: pointer;

                            &__Img {
                                width: 20px;
                            }

                            &__Text {
                                font-size: 12px;
                            }

                        }

                        &__No {
                            margin-left: 20px;
                            cursor: pointer;

                            &__Img {
                                width: 15px;
                            }

                            &__Text {
                                font-size: 10px;
                            }
                        }
                    }

                    
                }
            }
        }
    }
}
//2.8
@include tablet-p {
    .Questionnaire {
        position: absolute;
        top: 84px;
        right: 0;
        width: 365px;
        height: 220px;
        box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
        // display: flex;
    
        &__Container {
            width: 291px;
            height: 144.4px;
            margin: auto;

            &__Holder {
                margin-left: 15px;
                margin-top: 10px;
                padding: 20px 40px;

                &__Title {
                    margin-bottom: 15px;
                }
        
                &__Options {
        
                    &__Container {
                        &__Yes {
                            cursor: pointer;
                        }
        
                        &__No {
                            cursor: pointer;
                        }
                    }
                
                }
            
    
            }
        }
    }
}

@include desktop {
    .Questionnaire {
        position: absolute;
        top: 84px;
        right: 0;
        width: 443px;
        height: 290px;
        box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
        // display: flex;
    
        &__Container {
            width: 265px;
            height: 126px;
            margin: auto;

            &__Holder {
                margin-left: 0px;
                margin-top: 0px;
                padding: 20px 30px;

                &__Title {
                    margin-bottom: 5px;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 23px;
                }
        
                &__Options {
        
                    &__Container {
                        &__Yes {
                            cursor: pointer;
                        }
        
