.QuestionnaireCardHome {
    margin: auto;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;

    &__Blur {
        position: fixed;
        height: 300px;
        width: 750px;
        filter: blur(5px);
        background-color: rgba(129, 129, 129, 0.589);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    
    &__Container {
        margin: auto;
        z-index: 10;

        &__Top {
            display: flex;
            margin: 0px 0 0;
        }

        &__Bottom {
            display: flex;
            margin: 100px 0 0;
            justify-content: center;

            &__Span {
                cursor: pointer;
            }
        }
    }
    
}