@import '../../assets/mixins/mixins.scss';

.InputBox {
    height: 30px;
    width: 100px;
    border-radius: 10px;
    border-color: #707070;
    align-self: center;
    text-align: center;
    justify-self: center;
    display: inline-flex;
    font-family: Arial, Helvetica, sans-serif;
    margin-right: 20px;

    &::placeholder {
        font-size: 15px;
        font-family: Arial, Helvetica, sans-serif;
        color: rgba(36, 43, 58, 0.35);
        margin: auto 0;
    }
}

@include tablet-p {
    .InputBox {
        height: 40px;
        width: 120px;
        border-radius: 10px;
        border-color: #707070;
        align-self: center;
        text-align: center;
        justify-self: center;
        display: inline-flex;
        font-family: Arial, Helvetica, sans-serif;
    
        &::placeholder {
            font-size: 12px;
            font-family: Arial, Helvetica, sans-serif;
            color: rgba(36, 43, 58, 0.35);
            margin: auto 0;
        }
    }
}

@include desktop {
    .InputBox {
        height: 27px;
        width: 119px;
        border-radius: 10px;
        border-color: #707070;
        align-self: center;
        text-align: center;
        justify-self: center;
        display: inline-flex;
        font-family: Arial, Helvetica, sans-serif;
    
        &::placeholder {
            font-size: 10px;
            font-family: Arial, Helvetica, sans-serif;
            color: rgba(36, 43, 58, 0.35);
            margin: auto 0;
        }
    }
}