@import '../../assets/mixins/mixins.scss';

.Cards {
    height: calc(80px);
    background-color: rgb(255, 255, 255);
    position: relative;

    &__Container {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        position: absolute;
        top: -282px;
    }
}

@include tablet-p {
    .Cards {
        background-color: rgba(177, 177, 177, 0);
        position: relative;
    
        &__Container {
            margin-top: 0px;
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            position: relative;
            top: 0px;
            padding: 0 15px;
        }
    }
}