@import '../../../assets/mixins/mixins.scss';

.QuestionnaireViewer {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: #80808085;
    z-index: 500;

    &__Container {
        margin: auto;
        background-image: url('../../../assets/imgs/solar.jpeg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 90%;
        height: 60%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;

        &__Blur {
            margin: auto;
            width: 90%;
            backdrop-filter: blur(30px);
            padding: 10px 10px;text-align: center;
        }

        &__X {
            color:#51B03E;
            position: fixed;
            top: 60px;
            right: 60px;
            font-family: Arial, Helvetica, sans-serif;
            font-size: 36px;
            font-weight: bold;
            cursor: pointer;
        }
    }
}

@include desktop {
    .QuestionnaireViewer {

        &__Container {
            width: 60%;
        }
    }
}