@import '../../assets/mixins/mixins.scss';

.Card {
    width: 355px;
    height: 98px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.16);
    background-color: white;
    display: flex;
    cursor: pointer;
    position: relative;
    transition: all ease-in 0.2s;
    border-radius: 5px;
    margin: 0 auto;

    &:not(:first-of-type) {
        margin-top: -15px;
        box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.16), 0 5px 15px rgba(0, 0, 0, 0.16);
    }

    &__Text {
        font-size: 30px;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        color: #51B03E;
        margin: auto;
        text-align: center;
    }
}
//1.0677
@include tablet-p {

    .Card {
        width: 177.9px;
        height: 177.9px;
        box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.16), 0 5px 15px rgba(0, 0, 0, 0.16);
        background-color: white;
        display: flex;
        cursor: pointer;
        position: relative;
        top: -99px;
        transition: all ease-in 0.2s;

        &:not(:first-of-type) {
            margin-top: 0;
        }
    
        &:hover {
            top: -40px;
        }
    
        &__Text {
            font-size: 28px;
            font-family: Arial, Helvetica, sans-serif;
            color: #51B03E;
            margin: auto;
            text-align: center;
            padding: 0 11px;
        }
    }
}

@include desktop {
    .Card {
        width: 211px;
        height: 269px;
        box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.16), 0 5px 15px rgba(0, 0, 0, 0.16);
        background-color: white;
        display: flex;
        cursor: pointer;
        position: relative;
        top: 0;
        transition: all ease-in 0.2s;

        &:not(:first-of-type) {
            margin-top: 0;
        }
    
        &:hover {
            top: -40px;
        }
    
        &__Text {
            font-size: 26px;
            font-family: Arial, Helvetica, sans-serif;
            color: #51B03E;
            margin: auto;
            text-align: center;
            padding: 0 40px;
        }
    }
}